import axios from './index'
import { getStore } from '../config/storage'

export const login = (data) => {
    return axios.post('authority/login', data)
}

export const getCommunities = (data) => {
    return axios.get('community/list/' + data)
}

export const ifExistsCom = (name) => {
    return axios.get('community/nameExist/' + name)
}

export const getChannel = (id) => {
    return axios.get('channel/list/' + id)
}

export const modifyChannel = (id, params) => {

    return axios.post('channel/info/' + id, params, {
        headers: { 'Authorization': getStore("token") }
    })
}

export const delChannel = (id) => {
    return axios.delete('channel/delete/' + id, {
        headers: { 'Authorization': getStore("token") }
    })
}

export const addChannel = (data) => {
    return axios.put('channel/new', data, {
        headers: { 'Authorization': getStore("token") }
    })
}