<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>{{name}}</span>

      <el-button style="float: right; padding: 3px 0" @click="del" type="text">删除</el-button>
      <el-button style="float: right; padding: 3px 0" @click="modify" type="text">修改</el-button>
    </div>
    <div class="box-content">
      <div>
        <p>Id</p>
        <el-input v-model="id" disabled></el-input>
      </div>
      <div>
        <p>所属板块</p>
        <el-input v-model="sectionId" disabled></el-input>
      </div>
      <div>
        <p>名称</p>
        <el-input v-model="name"></el-input>
      </div>

      <div>
        <p>排序</p>
        <el-input v-model="zindex" placeholder="请输入内容"></el-input>
      </div>
      <div>
        <p>创建者</p>
        <el-input v-model="creator" placeholder="请输入内容" disabled></el-input>
      </div>
      <div>
        <p>创建时间</p>
        <el-input v-model="createTime" placeholder="请输入内容" disabled></el-input>
      </div>
      <div>
        <p>类型</p>
        
        <el-select v-model="type" placeholder="请选择" class="select">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div>
        <p>文章数</p>
        <el-input v-model="articleNum" placeholder="请输入内容" disabled></el-input>
      </div>
    </div>
  </el-card>
</template>
<script>
import { modifyChannel, delChannel } from "../api/accounts";
export default {
  props: {
    id: Number,
    name: String,
    sectionId: Number, //板块ID
    zindex: Number, //顺序
    creator: Number,
    createTime: String, //毫秒
    type: Number,
    articleNum: Number
  },
  data: () => {
    return {
      input: "",
      options: [
        {
          value: 0,
          label: "默认"
        },{
            value:1,
            label:"New"
        },
        {
            value:2,
            label:"Hot"
        }
      ]
    };
  },
  computed: {
    
  },
  methods: {
    modify() {
      modifyChannel(this.id, {
        name: this.name,
        zindex: Number(this.zindex),
        type: Number(this.type)
      }).then(res => {
        if (res.data.body == "success") {
          alert("修改已提交到服务器！");
        }
        console.log("ChannelCard after modify", res);
      });
    },
    del() {
        if(!confirm("确定要删除吗？")){
            return;
        }
      delChannel(this.id).then(res => {
        console.log("ChannelCard after del", res);
        location.reload();
      });
    }
  }
};
</script>
