<template>
  <div class="channel">
    <SideNav class="side"></SideNav>
    <div class="rest">
      {{Text}}
      <ChannelCard v-for="cl in channelList" :key="cl.id" v-bind="cl"></ChannelCard>
    </div>
    <div class="create" @click="centerDialogVisible = true"></div>
    <el-dialog title="新建频道" :visible.sync="centerDialogVisible" width="30%" center>
      <div class="dialog">
        <div>
          <span>板块Id</span>
          <el-input v-model="newItem.id" placeholder="请输入内容"></el-input>
        </div>
        <div>
          <span>频道名</span>
          <el-input v-model="newItem.name" placeholder="请输入内容"></el-input>
        </div>
        <div>
          <span>排序</span>
          <el-input v-model="newItem.zindex" placeholder="请输入内容"></el-input>
        </div>
      </div>

      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { ifExistsCom, getChannel,addChannel } from "../api/accounts";
import SideNav from "../components/SideNav";
import ChannelCard from "../components/ChannelCard";
import { convertTime } from "../config/tools";
export default {
  components: {
    SideNav,
    ChannelCard
  },
  mounted() {
    let id;
    ifExistsCom(this.$route.params.name).then(res => {
      id = res.data.body;
      if (!id) {
        this.Text = "404";
      } else {
        //获取到id后
        getChannel(id).then(res => {
          this.channelList = res.data.body;
          this.channelList.forEach(it => {
            it.createTime = convertTime(it.createTime);
          });
          console.log("Channel 成功获取信息", res.data.body);
        });
      }
    });

    console.log("Channel 路由", this.$route.params.name);
  },
  data: () => {
    return {
      Text: "",
      channelList: [],
      centerDialogVisible: false,
      newItem: {
        id: "",
        name: "",
        zindex: ""
      }
    };
  },
  methods:{
      submit(){
          this.centerDialogVisible = false;
          addChannel({
              sectionId:Number(this.newItem.id),
              name:this.newItem.name,
              zindex:Number(this.newItem.zindex)
          }).then(res=>{
              console.log('Channel ',res)
              if(res.data.body=="err"){
                  alert("提交失败！请检查输入")
                  return;
              }
              location.reload()
          })

      }
  }
};
</script>

