<script>
import HelloWorld from './components/HelloWorld.vue'


export default {
  name: 'app',
  components: {
    HelloWorld
  },
  mounted(){
    let storageTokenTime=localStorage.getItem('time');
    if(Date.now()-storageTokenTime>=20*60*60*1000){
      //需要重新登录
      console.log('App Need Login '+(Date.now()-storageTokenTime)/1000)
      this.$router.push('/login')
    }
  }
}
</script>

