<template>
  <el-card class="card">
    <el-input v-model="userName" placeholder="请输入用户名" style="margin-bottom:10px"></el-input>
    <el-input placeholder="请输入密码" v-model="password" show-password></el-input>
    <el-button @click="commit" type="primary" style="margin-top:10px">登陆</el-button>
  </el-card>
</template>
<script>
import Axios from "axios";
import { login } from "../api/accounts";
import { setStore, getStore } from "../config/storage";
export default {
  data: () => {
    return {
      userName: "",
      password: ""
    };
  },
  methods: {
    commit() {
      console.log("Login Post");
      login({ username: this.userName, password: this.password }).then(res => {
        if (res.data.body.search("用户名或密码错误") >= 0) {
          alert("用户名或密码错误！");
          return;
        }
        //成功登陆
        setStore("token", "Bearer " + res.data.body);
        setStore("time", Date.now().toString());
        console.log("Login " + getStore("token"));
        console.log("Login " + getStore("time"));
        this.$router.push("dashboard");
      });
    }
  },
  mounted() {
    //如果已登陆则跳转
    let storageTokenTime=localStorage.getItem('time');
    if(Date.now()-storageTokenTime<20*60*60*1000){
      //已保存登陆状态
      console.log('App Already Login')
      this.$router.push('/dashboard')
    }
  }
};
</script>
<style lang="stylus" scoped>
.card {
  width: 300px;
}
</style>
